@import "../base/settings";
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap");

.overlay {
	background-color: white;
	position: fixed;
	height: 100vh;
	width: 100%;
	overflow: hidden;
	opacity: 1;
	top: 0px;
	right: 0px;
	z-index: 1000;
}

.logReg {
	position: fixed;
	left: 50%;
	top: 50vh;
	transform: translate(-50%, -50%);

	height: 90vh;
	min-height: 550px;
	width: 60%;
	background: white 0% 0% no-repeat padding-box;
	border: 1px solid #c5c5c5;
	opacity: 1;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	box-shadow: 4px -1px 50px rgb(194, 194, 194);
	z-index: 2000;
	font-family: "Lato", sans-serif;

	input {
		margin-top: 10px;
		// margin-bottom: 10px;
	}
}

input {
	background: #ffffff 0% 0% no-repeat padding-box;
	border: solid 0.5px #707070;
	opacity: 1;
	width: 70%;
	min-height: 5px;
	max-height: 20px;
	height: 3.6vh;

	font-size: 0.7rem;
	padding: 5px;
	text-align: center;
}
input:focus {
	outline: none;
	border: 1px solid greenyellow;
}

button {
	// padding: 5px;
	width: 30%;
	font-size: 20px;
	background-color: white;
	border-radius: 100px;
	cursor: pointer;
}
button:active {
	background-color: $l_pri_color;
}
button:focus {
	outline: none;
}

.btnbtn {
	margin-top: 10px;
	border: solid 1px $grey_text;
}

.gotohomebtn {
	height: 20px;
	z-index: 3000;
	cursor: pointer;
}

.login_head {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 50px;
	color: $pri_color;
	border-bottom: solid 2px $pri_color;
	font-family: "Lato" sans-serif;
	font-size: 0.8rem;
	font-weight: 300px;
	letter-spacing: 1.68px;
	img {
		position: absolute;
		right: 30px;
	}
}

.loadingsvg {
	height: 16px;
	animation-name: rotate;
	animation-duration: 1s;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
	margin: 0px;
}

@keyframes rotate {
	from {
		transform: rotateZ(0deg);
	}
	to {
		transform: rotateZ(-360deg);
	}
}

.login_body {
	display: flex;
	flex-direction: row;
	width: 100%;
	padding: 30px;
}

.login_body_left {
	border-right: solid 2px $pri_color;
	width: 50%;
	padding: auto 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.google-button {
	height: 40px;
	width: 200px;
	border-width: 0;
	background: white;
	color: #737373;
	border-radius: 5px;
	white-space: nowrap;
	box-shadow: 1px 1px 0px 1px rgba(0, 0, 0, 0.05);
	transition-property: background-color, box-shadow;
	transition-duration: 150ms;
	transition-timing-function: ease-in-out;
	padding: 0;

	&:focus,
	&:hover {
		box-shadow: 1px 4px 5px 1px rgba(0, 0, 0, 0.1);
	}

	&:active {
		background-color: #e5e5e5;
		box-shadow: none;
		transition-duration: 10ms;
	}
}

.google-button__icon {
	display: inline-block;
	vertical-align: middle;
	margin: 8px 0 8px 8px;
	width: 18px;
	height: 20px;
	box-sizing: border-box;
}
.google-button__text {
	display: inline-block;
	vertical-align: middle;
	padding: 0 24px;
	font-size: 14px;
	font-weight: bold;
	font-family: "Roboto", arial, sans-serif;
}

.login_body_right {
	// border: solid 1px black;
	width: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding-bottom: 50px;
	button {
		margin: 10px;
	}
}

.socialbtn {
	width: 260px;
	height: 50px;
	border-radius: 0px;
	font-size: 14px;
	background: #ffffff 0% 0% no-repeat padding-box;
	background-position: 15px center;
	background-size: 25px;
	border: none;
	box-shadow: 0px 3px 6px #00000029;
	color: #707070;
	padding-left: 30px;
}
.google_btn {
	background-image: url("../../img/svg/google.svg");
}

.fb_btn {
	background-image: url("../../img/svg/logotype.svg");
}

.disabled {
	opacity: 0.7;
	text-decoration: line-through;
}

.or_login {
	position: absolute;
	height: 50px;
	width: 50px;
	padding: 5px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: white;
	border: solid 1px $pri_color;
	font-size: 1.7rem;
	color: $grey_text;
	border-radius: 100px;
}

.login_cont {
	// border: solid 1px black;

	display: flex;
	justify-content: space-between;
	flex-direction: column;
	width: 80%;

	h2 {
		font-weight: 300;
		color: $grey_text;
	}

	label {
		color: $grey_text;
		font-size: 0.8rem;
		font-weight: 300;
	}
	.forget_pass {
		font-size: 0.7rem;
		float: left;
		align-self: center;
		margin-right: -70px;
	}

	button {
		margin-top: 5vh;
		align-self: center;
	}
	a {
		color: #2699fb;
		cursor: pointer;
	}

	.login_cont_head {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;

		span {
			color: $grey_text;
			font-size: 0.85rem;
			font-weight: 200;

			font-family: Arial, Helvetica, sans-serif;
		}
	}
}

#rem_me_con {
	display: flex;
	flex-direction: row;
	justify-content: start;
	align-items: center;
	height: 20px;
	span {
		margin-top: -5px;
		font-size: 0.7rem;
		color: $grey_text;
	}
}

#rem_me {
	width: 15px;
}

.margin {
	// margin-bottom: 1vh;
	padding-bottom: 2px;
	// overflow: hidden;
}
#mob_num {
	width: 163px;
}
#c_code {
	width: 45px;

	height: 30px;
	border: solid 1px #707070;
	margin-right: 3px;
	transform: translateY(-1px);
}

#c_code:focus {
	outline: none;
}
